import React, { Component } from 'react'
import { NavLink, withRouter } from 'react-router-dom'
import { RoomContext } from '../context'
import Banner from '../components/Banner'
import StyledHero from '../components/StyledHero'

class SingleRoom extends Component {
  constructor(props){
    super(props)
    this.state = {
      slug: this.props.match.params.slug
    }
  }
  static contextType = RoomContext

  render() {
    const {getRoom} = this.context
    const room = getRoom(this.state.slug)

    if(!room) return (
      <div className='error'>
        <h3>no such room could be find...</h3>
        <NavLink to='rooms' className='btn-primary'>back to rooms</NavLink>
      </div>
    )
    const {name, description, capacity, price, size, extras, breakfast, pets, images} = room
    const [mainImg, ...defaultImg] = images
    return (
      <>
      <StyledHero img={mainImg}>
        <Banner title={`${name} room`}>
          <NavLink to='/rooms' className='btn-primary'>back to rooms</NavLink>
        </Banner>
      </StyledHero>
      <section className="single-room">
        <div className="single-room-images">
          {defaultImg.map((item,index) => 
            <img key={index} src={item} alt="room"/>
            )}
        </div>
        <div className="single-room-info">
          <article className="desc">
            <h3>details</h3>
            <p>{description}</p>
          </article>
          <article className="info">
            <h3>info</h3>
            <h6>price: ${price}</h6>
            <h6>size: {size}SQFT</h6>
            <h6>
              max capacity: {
                capacity > 1 ? capacity + ' people' : capacity + ' persone'
              }
            </h6>
            <h6>{pets? 'pets allowed' : 'no pets allowed'}</h6>
            <h6>{breakfast && 'free breakfast included'}</h6>
          </article>
        </div>
      </section>
      <section className="room-extras">
        <h6>extras</h6>
        <ul className='extras'>
              {extras.map((item, index)=> 
              <li key={index}>- {item}</li>
              )}
        </ul>
      </section>
      </>
    )
  }
}

export default withRouter(SingleRoom)
